.tagesschrift-regular {
  font-family: "Tagesschrift", sans-serif;
  font-weight: 400;
  font-style: normal; }

* {
  margin: 0;
  font-size: 20px; }

body {
  background: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  align-content: center;

  p, h1, h2, h3, h4, h5, span {
    font-family: tagesschrift;
    color: #ffc7d6; } }

#blurCursor {
  position: fixed;
  width: 150px;
  height: 150px;
  background: rgba(255, 0, 81, 0.4);
  border-radius: 50%;
  pointer-events: none;
  filter: blur(40px);
  transform: translate(-50%, -50%);
  z-index: 9999; }


@keyframes pulse-glow {
  0% {
    box-shadow: 0 0 10px rgba(255, 150, 200, 0.4), 0 0 20px rgba(255, 150, 200, 0.3), 0 0 30px rgba(255, 150, 200, 0.2); }
  50% {
    box-shadow: 0 0 25px rgba(255, 150, 200, 0.7), 0 0 50px rgba(255, 150, 200, 0.4), 0 0 70px rgba(255, 150, 200, 0.3); }
  100% {
    box-shadow: 0 0 10px rgba(255, 150, 200, 0.4), 0 0 20px rgba(255, 150, 200, 0.3), 0 0 30px rgba(255, 150, 200, 0.2); } }

.card {
  display: grid;
  max-width: 300px;
  padding: 15px;
  margin: auto;
  border: 1px solid pink;
  background: rgba(0, 0, 0, 0.2);
  animation: pulse-glow 2s infinite ease-in-out;
  transition: box-shadow 0.3s ease-in-out;


  &-title {
    justify-self: center;
    letter-spacing: 2px; }

  &-body {
    display: grid;
    row-gap: 10px;
    width: 100%; }

  &-image {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 300px;
    overflow: hidden;
    position: relative;
    border: 1px solid pink;

    &-content {
      width: 100%;
      border: 2px solid black; } }

  &-description, &-date, &-footer {
    justify-self: center; } }

.trail-dot {
  position: fixed;
  width: 30px;
  height: 30px;
  background: rgba(255, 0, 81, .1);
  border-radius: 50%;
  pointer-events: none;
  filter: blur(8px);
  transform: translate(-50%, -50%);
  z-index: 9998;
  animation: fadeOut 0.6s forwards; }

@keyframes fadeOut {
  to {
    opacity: 0;
    transform: translate(-50%, -50%) scale(0.5); } }
.card-image {
  position: relative;
  width: 100%;
  overflow: hidden; }  // Oculta as imagens fora da área visível

.image-slider {
  display: flex;
  transition: transform 1s ease-in-out;
  width: 300%; }

.slider-image {
  width: 100%;
  object-fit: cover;
  height: auto; }


// Animação de deslizamento automático
@keyframes slideAnimation {
  0% {
    transform: translateX(0); }  // Primeira imagem
  33% {
    transform: translateX(-100%); }  // Segunda imagem
  66% {
    transform: translateX(-200%); }  // Terceira imagem
  100% {
    transform: translateX(0); } }  // Volta à primeira imagem

// Adiciona a animação de loop infinito
.image-slider {
  animation: slideAnimation 9s infinite; }
